import React, {useState, useEffect} from 'react';
import Note from './components/Note.jsx';
import AddNote from './components/AddNote.jsx';
// import axios from 'axios';
import comm from './services/notes.js'
const App = () => {
   const [notes, setNotes] = useState([]);
   const [newNote, setNewNote] = useState("");
  
   useEffect(() => {
   //  const fetchNotes = async () => {
   //      let response = await fetch('http://localhost:3001/notes');
   //      let notes = await response.json();
   //      setNotes(notes);
   //  }
   //  fetchNotes();
   let response = comm.getAll();
   response.then(data => setNotes(data));

   }, []);

   const getNote = e => setNewNote(e.target.value);

   const addNote = e => {
     e.preventDefault();
     let noteToAdd = {
        content: newNote,
      //   date: new Date().toDateString(),
        important: Math.random() > 0.5 ? true : false
     }

   // axios.post('http://localhost:3001/notes', noteToAdd)
   // .then(response => {
   //    setNotes(notes.concat(response.data));
   //    setNewNote("");
   // })

   comm
   .create(noteToAdd)
   .then(response => {
      setNotes(notes.concat(response.data));
      setNewNote("");
   })
   }
   


   const toggleImportance = (id) => {
      // const url = `http://localhost:3001/notes/${id}`
      const note = notes.find(note => note.id === id);
       const changedNote = {...note, important: !note.important};
       

      //  axios.put(url, changedNote)
      //  .then(response => {
      //    console.log(response)
      //    let newNotes = [...notes];
      //    newNotes.splice(newNotes.indexOf(note), 1, response.data);    // array tha containes the removed elements
      //    setNotes(newNotes);
      //  })

      comm
      .update(id, changedNote)
      .then(response => {
         let newNotes = [...notes];

         newNotes.splice(newNotes.indexOf(note), 1, response.data);    // array that containes the removed elements
         setNotes(newNotes);
       })
   }

   const deleteEntry = id => {
     comm
     .deleteEntry(id)
     .then(response => {
      if(response.status === 204) {

         const newNotes = notes.filter(note => note.id !== id);
         setNotes(newNotes);
      }
     }

      )
   }


   return(
    <>
    <ul>
    {
      notes.map(note => {
         return <Note key = {note.id} note = {note} toggleImportance = {() => toggleImportance(note.id)} deleteEntry = {() => deleteEntry(note.id)}/>
      })
    }
    </ul>
    
    <AddNote note = {newNote} getNote = {getNote} onClick = {addNote}/>
    </> 
   )
}

export default App;
